@keyframes bounce-10 {
  0% {
    transform: translateY(0%);
  }
  50% {
    transform: translateY(7.5%);
  }
  100% {
    transform: translateY(0%);
  }
}

@keyframes bounce-20 {
  0% {
    transform: translateY(0%);
  }
  50% {
    transform: translateY(15%);
  }
  100% {
    transform: translateY(0%);
  }
}

@keyframes flip {
  from {
    transform: rotateX(0deg);
  }
  to {
    transform: rotateX(180deg);
  }
}

@keyframes flip-reverse {
  from {
    transform: rotateX(180deg);
  }
  to {
    transform: rotateX(0deg);
  }
}
